export default {
	data() {
		return {
			clickOk: false,
		};
	},
	methods:{
		gotoChange(){
			uni.navigateTo({
				url: '/pages/loginAll/changePhone'
			})
		},
		clickProvider(){
			let vm = this
			uni.showModal({
			    title: '提示',
			    content: '是否确定更换绑定的微信号？',
			    success: function (res) {
			        if (res.confirm) {
			            vm.clickOk = true
						vm.loginForProvider()
			        } else if (res.cancel) {
			            vm.clickOk = false
			        }
			    }
			});
		},
		loginForProvider(){
			var vm = this;
			uni.getProvider({
			    service: 'oauth',
			    success: function (res) {
			        if (~res.provider.indexOf('weixin')) {
			        	uni.login({
			        		provider: 'weixin',
			        		success: function(loginRes) {
								console.log(loginRes)
			        			vm.authorization = loginRes.code // 获取code
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => { //这里请求接口
									  console.log(info);
									},
									fail: () => {
									  uni.showToast({title:"微信登录授权失败",icon:"none"});
									}
								  })
			        		},
			        		fail(err) {
			        			uni.showToast({
			        				title: '授权失败！',
			        				icon: 'none'
			        			});
			        		}
			        	});
			        }else{
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon:"none"
						});
					}
			    }
			});
		},
	}
}
